.contactFormName {
  width: 600px;
  border: solid 1px;
  border-radius: 4px;
  border-color: rgba(220, 214, 205, 0.913);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

.contactFormNumber {
  width: 600px;
  border: solid 1px;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  padding-bottom: 10px;
  align-items: center;
}

.addBtn {
  margin-bottom: 10px;
  width: 100px;
  background-color: rgb(94, 169, 101);
  color: white;
  border-radius: 4px;
  border: none;
  line-height: 1.5;
}
